<template>
  <section class="empbody">
    <div class="header">社員マスタ</div>
    <div class="controller">
      <div>
        <p>検索条件:</p>
        <span>{{filterAsText}}</span>
      </div>
      <div>
        <b-button variant="none" class="btn-white" @click="showPopupSearch = true">検索条件</b-button>
        <b-button variant="none" class="btn-white" @click="resetList">検索条件リセット</b-button>
      </div>
    </div>
    <div class="employee">
      <div class="filter">
        <b-form-select v-model="request.sortField" :options="[{ value: 'shainCd==asc', text: '社員コード(昇順)' }, { value: 'shainCd==desc', text: '社員コード(降順)' }]" @change="getData"></b-form-select>
        <b-form-select v-model="request.limitNumber" :options="[{ value: '20', text: '20件' },{ value: '50', text: '50件' }]" @change="getData"></b-form-select>
      </div>
      <div class="main-table testTable" >
        <table @scroll="fixScrollTable">
          <thead>
          <tr>
            <th v-for="(field, index) in fields" :key="index" class="p-0" :class="{ 'has-border' : index == 1 }"
                :style="{left:  parseInt(`${ index < 2 ? fixLeft : 0 }`)  + 'px'}">
              {{field.label}}
            </th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="(shain, index) in shains" :key="index">
            <td  class="p-0" v-for="(field, fieldIndex) in fields" :key="fieldIndex" :class="{ 'has-border' : fieldIndex == 1 }"
                :style="{left: parseInt(`${ fieldIndex < 2 ? fixLeft : 0 }`)   + 'px'}">
              <span v-if="field.displayAs === 'checkbox'">
                  <span v-if="shain[field.fieldName]">
                      &#10004;
                  </span>
                  <span v-else-if="field.fieldName === 'isDeleted'">
                    <span v-if="shain.shainCdNk"></span>
                    <span v-else>&#10004;</span>
                  </span>
              </span>
              <span v-else>
                  <span v-if="field.fieldName === 'bumonNm'">
                      {{shain.mstBumonResponse.bumonNm}}
                  </span>
                  <span v-else-if="field.fieldName === 'positions'">
                    <span v-for="(position, positionIndex) in shain.mstRelYakushokuShainResponses" :key="positionIndex">
                      {{position.mstYakushokuResponse.name}}
                      <span v-if="positionIndex+1 < shain.mstRelYakushokuShainResponses.length">、 </span>
                    </span>
                  </span>
                  <span v-else>
                      {{shain[field.fieldName]}}
                  </span>
              </span>

            </td>
          </tr>
          </tbody>
        </table>
      </div>
      <b-pagination
            align="right"
            v-model="currentPage"
            :total-rows="totalItemsPageable"
            :per-page="request.limitNumber"
            :first-number="this.request.pageNumber == 0"
            :prev-class="{'d-none' : this.request.pageNumber == 0}"
            :last-number="this.request.pageNumber == this.totalPage"
            :next-class="{'d-none' : this.request.pageNumber == this.totalPage}"
            class='mr-5'
            @change="onPageChanged($event)" ></b-pagination>
    </div>
    <AdminSearchDialog v-model="showPopupSearch">
      <div class="supplieName">検索条件
      </div>
      <b-row>
        <b-col cols="9">
          <b-form-group label="社員コード" label-for="employee_code" class="label_input searchEmployee mt-5">
            <b-form-input v-model="filter.shainCd" type="text" id="employee_code"></b-form-input>
          </b-form-group>
        </b-col>
        <b-col cols="9">
          <b-form-group label="名前" label-for="employee_name" class="label_input searchEmployee mt-3">
            <b-form-input v-model="filter.shainNm" type="text" id="employee_name"></b-form-input>
          </b-form-group>
        </b-col>
        <b-col cols="9">
          <b-form-group label="部門" label-for="department_name" class="label_input searchEmployee mt-3">
            <b-form-select v-model="filter.bumonCd" :options="departmentSelectOptions" ></b-form-select>
          </b-form-group>
        </b-col>
        <b-col cols="9">
          <b-form-group label="役職" label-for="position_name" class="label_input searchEmployee mt-3">
            <b-form-select v-model="filter.positionCd" :options="positionSelectOptions" ></b-form-select>
          </b-form-group>
        </b-col>

        <b-col cols="9">
          <b-form-group label=" " label-for="include_deleted" class="label_input searchEmployee searchEmployee_checkbox mt-3">
            <b-form-checkbox size="lg" id="include_deleted" v-model="filter.includeDeleted" plain>削除済みを含める</b-form-checkbox>
          </b-form-group>
        </b-col>
      </b-row>
      <b-row class="search_recordAdmin">

      </b-row>
      <b-row class="mt-4">
        <b-col cols="8" align-self="end">
          <b-button-group>
            <b-button variant="none" class="btn-filter btn-white" @click="resetSearchForm">リセット</b-button>
            <b-button variant="none" class="btn-filter btn-blue" @click="onSearch">検索する</b-button>
          </b-button-group>
        </b-col>
      </b-row>
    </AdminSearchDialog>
  </section>
</template>

<script>
  import MasterService from "@/services/master/MasterService";
  export default {
    name: "Employees",
    data() {
      return {
        positionSelectOptions: [],
        departmentSelectOptions: [],
        currentPage: 1,
        totalPage: 0,
        totalItemsPageable: 0,
        filterAsText: '',
        shains: [],
        filter: {
          shainCd: '',
          shainNm: '',
          bumonCd: '',
          positionCd: '',
          includeDeleted: false
        },
        request: {
          sortField: 'shainCd==asc',
          pageNumber: 0,
          limitNumber: 20
        },
        showPopupSearch: false,
        fixLeft: 0,
        fields: [ { fieldName: 'shainCd', label: '社員コード', displayAs: 'text'},
          { fieldName: 'shainNm', label: '名前', displayAs: 'text'},
          { fieldName: 'bumonNm', label: '部門', displayAs: 'text'},
          { fieldName: 'positions', label: '役職', displayAs: 'text'},
          { fieldName: 'password', label: 'パスワード', displayAs: 'text'},
          { fieldName: 'isDeleted', label: '削除ステータス', displayAs: 'checkbox'},
          { fieldName: 'isAlertedForApplication', label:'申請', displayAs: 'checkbox'},
          { fieldName: 'isAlertedForModificationRequest', label: '修正依頼', displayAs: 'checkbox'},
          { fieldName: 'isAlertedForRejection', label: '却下', displayAs: 'checkbox'},
          { fieldName: 'isAlertedForApproval', label: '承認', displayAs: 'checkbox'},
          { fieldName: 'isAlertedForSendingRequestBack', label: '差し戻し', displayAs: 'checkbox'},
          { fieldName: 'isAlertedForConfirmingSendRequestBack', label: '差し戻し確認', displayAs: 'checkbox'},
          { fieldName: 'isAlertedForInputPaymentDate', label: '支払日入力', displayAs: 'checkbox'},
          { fieldName: 'isAlertedForConfirmingSettlement', label: '決裁確認', displayAs: 'checkbox'},
          { fieldName: 'isAlertedForApplicationDeputy', label: '申請代理人設定', displayAs: 'checkbox'},
          { fieldName: 'isAlertedForApprovalDeputy', label: '承認代理人設定', displayAs: 'checkbox'},
          { fieldName: 'isAlertedForChangingCharge', label: '担当者変更', displayAs: 'checkbox'},
          { fieldName: 'isAlertedForBeingCreated', label: '作成中', displayAs: 'checkbox'},
          { fieldName: 'isAlertedForWaitingConfirmation', label: '確認待ち', displayAs: 'checkbox'},
          { fieldName: 'isAlertedForWaitingApplication', label: '申請待ち', displayAs: 'checkbox'},
          { fieldName: 'isAlertedForWaitingApproval', label: '承認待ち', displayAs: 'checkbox'},
          { fieldName: 'isAlertedForWaitingApplicationOnSendingBack', label: '申請待ち(差し戻し中)', displayAs: 'checkbox'},
          { fieldName: 'isAlertedForWaitingApprovalOnSendingBack', label: '承認待ち(差し戻し中)', displayAs: 'checkbox'},
          { fieldName: 'isAlertedForWaitingConfirmingSettlement', label: '決裁確認待ち', displayAs: 'checkbox'},
          { fieldName: 'isAlertedForUpdatingDatabase', label: 'マスタ変更適用処理', displayAs: 'checkbox'}
        ]
      };
    },
    methods: {
      getData(){
        MasterService.getShains(this.request).then(res => {
          this.shains = res.data.content;
          this.totalItemsPageable = res.data.totalElements;
          this.totalPage = res.data.totalPages - 1;
        }).catch(err => {
          console.log(err);
        });
      },
      getFilterSelectOptions() {
        this.departmentSelectOptions = [{ value: '', text: '' }];
        this.positionSelectOptions = [{ value: '', text: '' }];
        MasterService.getFilterSelectOptions().then(res => {
          res.data.positions.forEach(position => {
            this.positionSelectOptions.push({ value: position.cd, text: position.name });
          });
          res.data.departments.forEach(department => {
            this.departmentSelectOptions.push({ value: department.bumonCd, text: department.bumonNm });
          });
        }).catch(err => {
          console.log(err);
        });
      },
      onSearch() {
          let filterItems = [];
          let filterText = [];
          this.currentPage = 1;
          this.request.pageNumber = 0;

          if (this.filter.shainCd) {
            filterItems.push("shainCd=='" + this.filter.shainCd + "%'");
            filterText.push('社員コード:' + this.filter.shainCd);
          }
          if (this.filter.shainNm) {
            filterItems.push("shainNm=='%" + this.filter.shainNm + "%'");
            filterText.push('名前:' + this.filter.shainNm);
          }
          if (this.filter.bumonCd) {
            filterItems.push("mstBumonEntity.bumonCd=='" + this.filter.bumonCd + "'");
            filterText.push('部門:' + this.departmentSelectOptions.find( dp => dp.value == this.filter.bumonCd).text);
          }
          if (this.filter.positionCd) {
            filterItems.push("mstRelYakushokuShainEntities.mstYakushokuEntity.cd=='" + this.filter.positionCd + "'");
            filterText.push('役職:' + this.positionSelectOptions.find( po => po.value == this.filter.positionCd).text);
          }
          if (this.filter.includeDeleted) {
            filterItems.push("(shainCdNk=='',shainCdNk==null)");
            filterText.push('削除済みを含める: true');
          }
          this.request.keyword = filterItems.join(";");
          this.filterAsText = filterText.join(" / ");
          this.getData();
          this.showPopupSearch = false;
      },
      onPageChanged(event) {
        this.request.pageNumber = event - 1;
        this.getData();
      },
      resetSearchForm() {
        this.filter.shainCd = '';
        this.filter.shainNm = '';
        this.filter.bumonCd = '';
        this.filter.positionCd = '';
        this.filter.includeDeleted = false;
        this.filterAsText = '';
        this.request.keyword = '';
      },
      resetList() {
        this.currentPage = 1;
        this.request.pageNumber = 0;
        this.resetSearchForm();
        this.getData();
      },
      fixScrollTable(event) {
        this.fixLeft = event.target.scrollLeft;
      }
    },
    created() {
      this.getFilterSelectOptions();
      this.getData();
    }
  };
</script>

<style lang='scss' scoped>
  .btn-filter {
    padding: 0.7rem 2rem;
    width: 10rem;
    height: 4rem;
    margin: 0 1vw;
  }
</style>